@import "../inc/config";

.panel {
  min-height: 100vh;
  width: 100%;
  display: flex;

  aside {
    width: 240px;
    background: $color1;

    .aside {
      width: 240px;
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
    }

    .logo {
      padding: 20px;
    }

    nav {
      ul {
        li {
          background: transparent;
          transition: all 0.5s;
          display: flex;
          align-items: center;

          a {
            margin: 14px 20px;
            display: block;
            width: 100%;
            color: #fff;
            font: 600 12px/14px $font;
            letter-spacing: 1px;
            text-transform: uppercase;
            transition: all 0.5s;
          }

          &:hover {
            background: $color2;
            transition: all 0.5s;

            a {
              color: $color0;
              transition: all 0.5s;
            }
          }

          &.active {
            background: $color2;

            a {
              color: $color0;
            }
          }
        }
      }
    }
  }

  main {
    flex: 1;
    padding: 40px;
    display: flex;
    flex-direction: column;

    .top-bar {
      display: flex;
      align-items: center;
      margin-bottom: 40px;

      h1 {
        display: flex;
        font: 500 16px/16px $font;
        letter-spacing: 2px;
        text-transform: uppercase;
        color: $color3;
        flex: 1;
      }

      button {
        width: 200px;
      }
    }
  }
}

.content {
  display: flex;

  > ul {
    width: 100%;

    > li {
      margin: 10px 0;
      border-radius: 15px;
      background: $color1;
      color: #fff;
      padding: 20px 30px;
      transition: all 0.5s;
      display: flex;
      align-items: center;
      font: 500 11px/11px $font;
      letter-spacing: 1px;
      text-transform: uppercase;
      cursor: pointer;
      .avatar {
        width: 32px;
        img {
          size:32px;
          border-radius: 50%;
        }
      }
      .title {
        margin-left: 20px;
        flex: 1;
        transition: all 0.5s;
      }
  .date-nums  {
    flex:1;
    display: flex;
    font: 500 14px/14px $font;
    letter-spacing: 2px;
    text-transform: uppercase;
    .number {
      color:$color2;

    }
    .month {
      margin-left: 10px;
    }
  }
      .date {
        width: 150px;
        color: $color3;
      }

      .cranes {
        width: 150px;
        color: $color3;
      }

      .contol {
        width: 150px;
        display: flex;

        li {
          margin: 0 5px;

          a {}
        }
      }

      &:hover {
        transition: all 0.5s;
        background: darken($color1, 2);

        .title {
          transition: all 0.5s;
          color: $color2;
        }
      }
    }
  }
}
.uk-form-controls.duble {
  display: flex;
  > div {
    width: 50%;
    &:first-child {
      margin-right: 5px;
    }
    &:last-child {
      margin-left: 5px;
    }
  }
}
  .add-news {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    .uk-margin, .uk-input, form {
      width: 100%;
    }
  }
